<template>
<div class="page" id="mine">
    <h1>个人</h1>

    <button @click="pickPhotoAction">选择照片</button>
    <img class="header" :src="headerImg" alt="">
    
    <a href="#sheet">登录</a>

    <div id="sheet" class="mui-popover mui-popover-bottom mui-popover-action ">
		<!-- 可选择菜单 -->
		<ul class="mui-table-view">
		  <li class="mui-table-view-cell" @click="qqLoginAction">
			<a href="#">qq登录</a>
		  </li>
		  <li class="mui-table-view-cell">
			<a href="#">微信登录</a>
		  </li>
          <li class="mui-table-view-cell">
			<a href="#">微博登录</a>
		  </li>
		</ul>
		<!-- 取消菜单 -->
		<ul class="mui-table-view">
		  <li class="mui-table-view-cell">
			<a href="#sheet"><b>取消</b></a>
		  </li>
		</ul>
	</div>

</div>
</template>

<script>
export default {
    data(){
        return {
            headerImg: ''
        }
    },
    methods: {
        qqLoginAction(){
            console.log('点击了qq登录');
            mui('#sheet').popover('toggle');
        },

        pickPhotoAction(){
            plus.gallery.pick(
				(file)=>{
                    this.headerImg = file;
				},
				(error)=>{
					console.log(error.code);
					console.log(error.message);
				},
				{
					multiple: false
				}
			);
        }
    }
}
</script>

<style scoped>
.header{
    width: 100px;
    height: 100px;
    background: #ddd;
    border: 1px solid #333;
    border-radius: 50%;
}
</style>
